.sendForm {
    position: absolute;
    width: 976px;
    height: 40px;
    left: 70px;
    top: 432px;

    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
}

.textarea {
    position: absolute;
    left: 30px;
    top: 10px;
    background: #F5F7FB;
    width: 930px;
    height: 1px;


    border: none;
    overflow: auto;
    outline: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    resize: none;


}

